import cn from 'classnames';
import {useNavigate} from 'react-router-dom';
import {Button} from '@ya.praktikum/react-developer-burger-ui-components';
import {RegistrationForm} from '../../components/secure/registration-form/registration-form';
import registerPageStyles from './register.module.css';

export const RegisterPage = () => {

  const navigate = useNavigate();

  function goLogin() {
    navigate('/login');
  }

  return (
      <section className={registerPageStyles.page}>
        <h2 className='text text_type_main-medium mb-6'></h2>
        <RegistrationForm/>
        <div>
          <span className='text text_type_main-default text_color_inactive'> ?</span>
          <Button extraClass={cn(registerPageStyles.button, 'ml-2')}
                  htmlType='button'
                  type='secondary'
                  size='medium'
                  onClick={goLogin}>
            
          </Button>
        </div>
      </section>
  )
}
