import {useNavigate} from 'react-router-dom';
import cn from 'classnames';
import {Button} from '@ya.praktikum/react-developer-burger-ui-components';
import {AuthorizationForm} from '../../components/secure/authorization-form/authorization-form';
import loginPageStyles from './login.module.css';

export const LoginPage = () => {

  const navigate = useNavigate();

  function goRegister() {
    navigate('/register');
  }

  function goForgot() {
    navigate('/forgot-password');
  }

  return (
      <section className={loginPageStyles.page}>
        <h2 className='text text_type_main-medium mb-6'></h2>
        <AuthorizationForm/>
        <div>
          <span className='text text_type_main-default text_color_inactive'>   ?</span>
          <Button extraClass={cn(loginPageStyles.button, 'ml-2')}
                  htmlType='button'
                  type='secondary'
                  size='medium'
                  onClick={goRegister}>
            
          </Button>
        </div>
        <div>
          <span className='text text_type_main-default text_color_inactive'> ?</span>
          <Button extraClass={cn(loginPageStyles.button, 'ml-2')}
                  htmlType='button'
                  type='secondary'
                  size='medium'
                  onClick={goForgot}>
             
          </Button>
        </div>
      </section>
  )
}
